/*@tailwind base;
@tailwind components;
@tailwind utilities;*/
:root {
  --color-border: #6fee0773;
  --color-bg: #79f00913;
}

/* Estilos base para todos los dispositivos */
body {
  font-family: "IBM Plex Mono", monospace !important;
  margin: 0;
  padding: 0;
  background-color: #000;
  color: #fff;
  overflow-x: hidden;
}
/* Estilos para la tabla */
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th,
td { 
  padding: 8px;
  text-align: left;
  border: 1px solid var(--color-border);;
}

th {
  background-color: #e9dddd5e;
}
blockquote {
  background: #f9f9f929;
  padding: 0.5rem;
  margin: 1rem 0;
}
  /* Estilos para el formulario */
fieldset { 
    padding: 20px;
    border: 1px solid var(--color-border);
    border-radius: 5px;
}
input {
  width: 100%;
  padding: 1rem 0.5rem;
}
.react-p5{
  position: absolute;
  top: 0;
  right: 0;
  width: 100%; 
  z-index: -9;
}
p{
  margin-bottom: 1rem;
}
fieldset{
  margin-bottom: 1rem;
}
/* Estilos para los campos del formulario */
label,
input,
textarea {
    display: block;
    margin-bottom: 10px;
    border: 1px solid var(--color-border);
    background: var(--color-bg);
    color: white;
}

/* Estilos para el botón del formulario */
button {
  background-color: var(--color-border);
  color: white;
  border: none;
  padding: 1rem 1.5rem;
  cursor: pointer;
}
* {
  font-family: "IBM Plex Mono", monospace !important;
}

header { 
  padding: 10px; 
}

nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

nav li {
  display: inline;
  margin-right: 20px;
}

nav button {
  background: none;
  border: none;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
}

main {
  padding: 20px;
}
/* App.css */

.App {
  text-align: center;
}

.container {
  max-width: 480px;
  margin: 0 auto;
  padding: 16px;
}

/* Estilos para iconos */
.icon-container {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  justify-content: center;
}

.icon-item {
  cursor: pointer; 
}

/* Estilos para detalles de criptomoneda */
.detalles-criptomoneda {
  border: 1px solid #ccc;
  padding: 16px;
  margin-top: 16px;
}

/* Estilos para dispositivos móviles */
@media (max-width: 767px) {
  .container {
    width: 100%;
    max-width: 100%;
  }

  .icon-container {
    justify-content: center;
  }
}

/* Estilos para bordes de color verde clásico de los años 80 */
/* Puedes ajustar los valores de border-color para obtener el verde que desees */
h1, h2, h3, h4, h5, h6, p, li {
  padding-left: 10px;
}
/* Estilos para el contenedor de iconos */
.icon-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
* {
  word-break: break-all; 
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* Estilos para cada icono individual */
.icon-item {
  cursor: pointer; 
}

/* Estilos para el icono dentro de icon-item */
.icon-item img {
  width: 100%;
  height: 100%;
}

/* Media queries para estilos responsivos en diferentes tamaños de pantalla */
/* Aquí se aplica el tamaño de fuente más grande para dispositivos pequeños */
@media (max-width: 768px) {
  body {
    font-size: 16px;
  }
}

/* Para dispositivos medianos y grandes, la fuente se mantiene en 18px */
@media (min-width: 768px) {
  body {
    font-size: 18px;
  }
}
